body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 10px;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  flex: 1;
  padding: 8px;
}

.load-more {
  cursor: pointer;
  margin-top: 20px;
  padding: 10px;
  display: none;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.photo-card {
  width: calc(33.33% - 10px);
  box-sizing: border-box;
  margin-bottom: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  cursor: pointer;
  border-radius: 5px;
}

.info {
  background-color: #f0f0f0;
  border-radius: 5px;
  padding: 8px;
}

.info-item {
  margin: 0;
  padding: 5px 0;
}

